@import url(https://fonts.googleapis.com/css2?family=Caveat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Caveat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
    color: white;
}
#header{
    display: flex;
    justify-content: center;
    margin: 0; 
    padding: 21px;
    font-family: 'Caveat', cursive;
    font-size: 84px;
}

#moods{
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px double black;
}

#textareaBox{
    margin-bottom: 1rem;
    width: 100%;
    height: 200%;

    width:500px;
	overflow:scroll;
	background-color:#FFF;
	color:#222;
	font-family:Courier, monospace;
	font-weight:normal;
	font-size:24px;
	resize:none;
	line-height:40px;
	padding-left:100px;
	padding-right:100px;
	padding-top:45px;
	padding-bottom:34px;
	background-image:url(https://static.tumblr.com/maopbtg/E9Bmgtoht/lines.png), url(https://static.tumblr.com/maopbtg/nBUmgtogx/paper.png);
	background-repeat:repeat-y, repeat;
	border-radius:12px;
	box-shadow: 0px 2px 14px #000;
	border-top:1px solid #FFF;
	border-bottom:1px solid #FFF;

}

#cardBody{
    margin-left: auto;
    margin-right: auto;
    width: 50%;
    height: 4rem;
    border-radius: 5px;
}

.frame{
    background-color: #7D7ABC;
    width: 100vw; 
    height: 100vh;
}


.button-actions > button{
    padding: 5px 10px;
    margin-right: 5px;
    color: white;
    background: #EF767A;
    border: 1px solid white;
    border-radius: 5px;
    font-weight: bolder;
    font-size: 16px;
    cursor: pointer;
}

.slide-right {

    position: relative;
    left: 0px;
    -webkit-animation: slideIn 2s forwards;
    animation: slideIn 2s forwards;
}
@-webkit-keyframes slideIn {
    0% {
        transform: translateX(-900px);
    }
    100% {
        transform: translateX(0);
    }
}
@keyframes slideIn {
    0% {
        transform: translateX(-900px);
    }
    100% {
        transform: translateX(0);
    }
}

textarea { font-size: 18px; }

#footer{
    height: 5rem;
    width: 100%;
    position: absolute;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 16px;
}
#quoteBody{
    display: flex;
    justify-content: center;
}

.container-quote {
    margin: 0 auto;
    width: 50%;
}

.box-quote {
    height: 300px;
    width: 507px;
}

.box-quote > * {
    line-height: 1.5;
}

#button-quote {
    display: flex;
    justify-content: center
}

.quotation-mark-img {
    margin: 0 auto;
    height: 100px;
    width: auto;
    opacity: 0.5;
}

.quotetext {
    font-size: 35px;
    font-family: 'Caveat', cursive;
}

.author {
    font-size: 24px;
    text-align: right;
    font-family: 'Caveat', cursive;
}

.fade-in {
    animation: fadeIn ease 3s;
    -webkit-animation: fadeIn ease 3s;
    -moz-animation: fadeIn ease 3s;
    -o-animation: fadeIn ease 3s;
    -ms-animation: fadeIn ease 3s;
    }
    @keyframes fadeIn {
    0% {opacity:0;}
    100% {opacity:1;}
    }
    
    @-webkit-keyframes fadeIn {
    0% {opacity:0;}
    100% {opacity:1;}
    }
